
import React from 'react'
import Slider from "react-slick"
import CategoriesSlider from './Product/Categories/CategoriesSlider'

import imag2 from '..//../Asets/images/banner-4.jpeg'
import imag3 from '..//../Asets/images/blog-img-1.jpeg'
import imag4 from '..//../Asets/images/blog-img-2.jpeg'
import imag5 from '..//../Asets/images/grocery-banner-2.jpeg'
import imag6 from '..//../Asets/images/grocery-banner.png'

import imag8 from '..//../Asets/images/slider-2.jpeg'
import imag9 from '..//../Asets/images/slider-image-2.jpeg'
import imag10 from '..//../Asets/images/slider-image-3.jpeg'
import imag11 from '..//../Asets/images/slider-image-1.jpeg'
import Products from './Product/Products'
export default function Home() {
    let settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 2,
        slidesToScroll: 2,
        arrows: false,
    };
    const images = [imag2, imag3, imag4, imag5, imag6, imag8, imag9, imag10, imag11]


    return (
        <>
            <CategoriesSlider />
            <div className="row"><div className="col-md-12 my-5"><div className="row  ">
                <div className=" col-md-6">
                    <Slider {...settings} >

                        {images.map((img, index) => {
                            return <div key={index} className="slid text-center p-5 "><img src={img} className='w-100' alt="" /></div>
                        })}
                    </Slider>
                </div>
                <div className=" col-md-6">
                    <img className='w-100' src={imag5} alt="" /></div>
            </div>
            </div>

                <Products />
            </div>
        </>
    )
}
