
import Layout from './Component/Layout/Layout';
import Home from './Component/Homee/Home';
import Categories from './Component/Homee/Product/Categories/Categories';
import { Navigate, RouterProvider, createBrowserRouter } from 'react-router-dom'
import Notfound from './Component/Notfound';
import CountContextProvider from './Context/context';
import Regester from './Component/Protect&sign/Regester';
import Login from './Component/Protect&sign/Login';
import Protected from './Component/Protect&sign/Protected';
import Cart from './Component/Cart/Cart';
import ProductDetails from './Component/Homee/Product/ProductDetails';
import { ToastContainer } from 'react-toastify';

import CheckOrders from './Component/Orders/CheckOrders';
import Orders from './Component/Orders/Orders';
import CartContextProvider from './Context/CartContext';
import Products from './Component/Homee/Product/Products';
import { QueryClient, QueryClientProvider } from 'react-query';
// import { ReactQueryDevtools } from 'react-query/devtools';
import ResetPassword from './Component/Protect&sign/ResetPassword';
import SetCode from './Component/Protect&sign/SetCode';
import Brands from './Component/Homee/Product/Brands/Brands';
import WihList from './Component/Homee/witchlist/WishList';
import ForgotPassword from './Component/Protect&sign/ForgotPassword';


function App() {
  const queryClient = new QueryClient()
  const routers = createBrowserRouter([
    {
      path: '', element: <Layout />, children: [
        { path: '', element: <Navigate to={"/home"} /> },
        { path: 'home', element: <Protected><Home /></Protected> },
        { path: 'categories', element: <Protected> <Categories /></Protected> },
        { path: 'orders', element: <Protected> <Orders /></Protected> },
        { path: 'products', element: <Protected> <Products /></Protected> },
        { path: 'allorders', element: <Protected> <Cart /></Protected> },
        { path: 'brands', element: <Protected> <Brands /></Protected> },
        { path: 'wishlist', element: <Protected> <WihList /></Protected> },
        { path: 'check/:cartId', element: <Protected> <CheckOrders /></Protected> },
        { path: 'productdetails/:id', element: <ProductDetails /> },
        { path: 'regester', element: <Regester /> },
        { path: 'login', element: <Login /> },
        { path: 'forgetpassword', element: <ForgotPassword /> },
        { path: 'resetpassword', element: <ResetPassword /> },
        { path: 'setcode', element: <SetCode /> },
        { path: '*', element: <Notfound /> },
      ]
    }
  ])
  return <>
    <QueryClientProvider client={queryClient}>
      <CartContextProvider>
        <CountContextProvider >
          <RouterProvider router={routers} > </RouterProvider >
        </CountContextProvider >
      </CartContextProvider>
      {/* <ReactQueryDevtools /> */}
    </QueryClientProvider>
    <ToastContainer />

  </>
}

export default App;
