/* eslint-disable no-unused-expressions */
/* eslint-disable no-redeclare */
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useContext } from 'react'
import { Link } from 'react-router-dom'
import { toast } from 'react-toastify'
import Swal from 'sweetalert2'
import { cartContext } from '../../Context/CartContext'
export default function Cart({ removeProductFromCart }) {
    const { setCartcount } = useContext(cartContext)
    const [disable, setdisable] = useState(false)
    const [cart, setCart] = useState({})
    const [isLoading, setIsLoading] = useState(false)
    const [cartId, setCartId] = useState()
    async function getLogedCart() {
        try {
            setIsLoading(true)
            const { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/cart`, {
                headers: {
                    token: localStorage.getItem('token')
                }
            })
            setIsLoading(false)
            console.log(data)
            setCart(data.data)
            setCartId(data.data._id)
        } catch (error) {
            setIsLoading(false)
            console.log(error)
        }
    }
    function removeProductFromCart(productId) {
        const swalWithBootstrapButtons = Swal.mixin({
            customClass: {
                confirmButton: "btn btn-success",
                cancelButton: "btn btn-danger"
            },
            buttonsStyling: false
        });
        swalWithBootstrapButtons.fire({
            title: "Are you sure?",
            text: "You won't be able to revert this!",
            icon: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes, delete it!",
            cancelButtonText: "No, cancel!",
            reverseButtons: true
        }).then(async (result) => {

            if (result.isConfirmed) {
                let { data } = await axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/` + productId,
                    { headers: { token: localStorage.getItem('token') } })



                swalWithBootstrapButtons.fire({
                    title: "Deleted!",
                    text: "Your file has been deleted.",
                    icon: "success"
                });
                setCartcount(data)
                setCart(data.data);
            } else if (

                /* Read more about handling dismissals below */
                result.dismiss === Swal.DismissReason.cancel

            ) {

                swalWithBootstrapButtons.fire({
                    title: "Cancelled",
                    text: "Your imaginary file is safe :)",
                    icon: "error"
                });

            }
        });
    }
    function clearCart() {
        const swalWithBootstrapButtons = Swal.mixin({
            customClass: {
                confirmButton: "btn btn-success",
                cancelButton: "btn btn-danger"
            },
            buttonsStyling: false
        });
        swalWithBootstrapButtons.fire({
            title: "Are you sure?",
            text: "You won't be able to revert this!",
            icon: "warning",
            showCancelButton: true,
            confirmButtonText: "Yes, delete it!",
            cancelButtonText: "No, cancel!",
            reverseButtons: true
        }).then(async (result) => {
            if (result.isConfirmed) {

                let { data } = await axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`,
                    { headers: { token: localStorage.getItem('token') } })
                setdisable(true);
                setCartcount(0)
                setCart(data);
                toast.success("Cart cleared");
                swalWithBootstrapButtons.fire({
                    title: "Deleted!",
                    text: "Your file has been deleted.",
                    icon: "success"
                });
            } else if (
                /* Read more about handling dismissals below */
                result.dismiss === Swal.DismissReason.cancel
            ) {
                swalWithBootstrapButtons.fire({
                    title: "Cancelled",
                    text: "Your imaginary file is safe :)",
                    icon: "error"
                });
            }
        });

    }

    async function updateCart(productId, count) {
        if (count == 0) {
            removeProductFromCart(productId)
        } else {
            let { data } = await axios.put(`https://ecommerce.routemisr.com/api/v1/cart/` + productId, {
                count
            }, { headers: { token: localStorage.getItem('token') } }
            )
            setCart(data.data);
        }
    }
    useEffect(() => { getLogedCart() }, [])
    return (<>
        {isLoading ?
            <div className='d-flex align-items-center justify-content-center my-5 py-5'>
                <i className='fas fa-spin fa-spinner fa-2x'></i>
            </div>
            : <>
                {cart.products?.length <= 0 ? <h2 className='alert alert-warning text-center my-5'>No products in your cart</h2> : <><div className='my-5'>
                    <button onClick={clearCart} disabled={disable} className='btn btn-outline-danger d-block ms-auto'>Clear Cart</button>


                    {cart.products?.map((product, index) => {
                        return <div key={index} className="cartproduct shadow rounded-2 my-3" >
                            <div className="row align-items-center p-3">
                                <div className="col-md-2">
                                    <img className='w-100' src={product.product.imageCover} alt="" />
                                    <span className=' m-2 text-main'>
                                        <i className="fas fa-star rating-color me-1"></i>
                                        {product.product.ratingsAverage}
                                    </span>
                                </div>
                                <div className="col-md-8">
                                    <h2>{product.product.title}</h2>
                                    <h5>{product.product.category.name}</h5>
                                    <div className='d-flex justify-content-between'>

                                        <span >{product.price}</span>

                                    </div>
                                    <span > Total product price <span className='text-main'>{product.price * product.count}</span></span>
                                </div>
                                <div className="col-md-2">
                                    <button onClick={() => removeProductFromCart(product.product._id)} className='btn btn-danger mb-5 w-100'>Remove</button>
                                    <div className="d-flex align-items-center justify-content-between w-100">
                                        <button onClick={() => updateCart(product.product._id, product.count + 1)} className='btn bg-main text-light mx-2'>+</button>
                                        <span className='text-main fw-bolder'>{product.count}</span>
                                        <button onClick={() => updateCart(product.product._id, product.count - 1)} className='btn bg-main text-light mx-2'>-</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    })}


                </div>

                    <div className='d-flex justify-content-between'>
                        <button disabled={cart.products?.length === 0} className='btn bg-main text-white'> <Link className=' text-white' to={'/check/' + cartId}>CheckOut</Link></button>
                        <span>Total Cart price <span className='text-main'>{cart.totalCartPrice
                        }</span> </span>
                    </div>
                </>
                }</>}




    </>)
}
