import React from 'react'
import { Link } from 'react-router-dom'
export default function Notfound() {
    return (<>
        <div className="container py-5 my-5 text-center  text-black">
            <h1 className="text-danger">ERROR 404</h1>
            <p className="h3">PAGE NOT FOUND</p>
            <Link to={"/"}>
                <button className="btn btn-outline-dark"> GO TO HOME </button>
            </Link>

        </div>
    </>

    )
}
