import React, { useState } from 'react'

import { useFormik } from 'formik'
import * as Yup from 'yup'
import { useParams } from 'react-router-dom'
import axios from 'axios'



export default function CheckOrders() {
  const validationSchema = Yup.object({

    details: Yup.string().required("required field"),
    phone: Yup.string().required("required field").matches(/^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\s\./0-9]*$/i, "pleas writ the correct phone number"),
    city: Yup.string().required("required field")
  })
  const initialValues = {
    details: '',
    phone: '',
    city: '',
  }





  const [err, seterr] = useState('')
  const [isload, setload] = useState(false)
  let { cartId } = useParams()

  const { handleChange, values, handleSubmit, errors, handleBlur, touched, isValid } = useFormik({
    initialValues,
    onSubmit: async () => {
      seterr('')
      setload(true)
      try {
        console.log(values)
        let { data } = await axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cartId}`,
          { shippingAddress: values }, {
          headers: {
            token: localStorage.getItem('token')
          }, params: {
            url: 'http://localhost:3000'
          }
        })


        window.open(data.session.url, '_self')


      } catch (error) {

        seterr(error.response.data.message)
      }
      setload(false)
    },
    validationSchema
  })



  return (
    <>
      <form onSubmit={handleSubmit}>
        <div className="card px-4">
          <p className="h8 py-3 text-center">ADRESS</p>
          <div className="col-12">
            <label htmlFor="detail1" className="form-label mt-2">DATAILS</label>
            <input type="text" className="form-control " onBlur={handleBlur} onChange={handleChange} value={values.details} id="detail1" name="details" />
            {errors.details && touched.details && <p className="alert alert-warning my-2">{errors.details}</p>}
          </div>
          <div className="col-12">
            <label htmlFor="phone1" className="form-label mt-2">PHONE-NUMBER</label>
            <input type="phone" className="form-control " onBlur={handleBlur} onChange={handleChange} value={values.phone} id="phone1" name="phone" />
            {errors.phone && touched.phone && <p className="alert alert-warning my-2">{errors.phone}</p>}
          </div>
          <div className="col-12">

            <label htmlFor="city1" className="form-label mt-2">CITY</label>
            <input type="text" className="form-control " onBlur={handleBlur} onChange={handleChange} value={values.city} id="city1" name="city" />
            {errors.city && touched.city && <p className="alert alert-warning my-2">{errors.city}</p>}

          </div>

          <div className="col-12">

            {isload ? <button type="submit" disabled="" className="btn btn-primary ms-auto my-2 px-4 d-block"><i className="px-2 fa-solid fa-spinner fa-spin"></i></button>
              :
              <button type="submit" className="btn btn-primary my-3" disabled={!isValid} > <span className="ps-3">CHECK OUT</span> <span className="fas fa-arrow-right me-auto ms-2"></span></button>
            }
            {err && <div className="alert alert-danger">{err}</div>}
          </div>
        </div>
      </form >

    </>
  )
}
