import React, { useEffect, useState } from 'react'
import { jwtDecode } from "jwt-decode";
import axios from 'axios';
export default function Orders() {
    const [orders, setOrders] = useState([])

    async function getAllOrders(id) {
        const { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/orders/user/` + id);
        setOrders(data);
        console.log(data);
    }
    useEffect(() => {
        const { id } = jwtDecode(localStorage.getItem('token'));
        getAllOrders(id)
    }, [])
    return (
        <>
            {orders?.map((order) => {
                return <div key={order.id} className="row " >
                    <div className=" shadow rounded-2 my-5 p-5 ">
                        <div className="d-flex align-items-center">
                            <h2 className="fw-bolder h1">
                                #{order.id}
                            </h2>
                            <h4 className='fw-bold text-primary mx-4'>processing</h4>
                        </div>
                        <p>you have ordered {order.cartItems.length}</p>

                        <div className="d-flex flex-wrap">{order.cartItems.map((i) => {
                            return <div key={i._id} className='p-2' >
                                <img style={{ width: 150 }} src={i.product.imageCover} alt="" />
                                <p>{i.product.title.split(" ").slice(0, 2).join(" ")}</p>
                                <p>pices:{i.count}</p>
                                <span > Total product price {i.price * i.count}</span>
                                <span className=' m-2 text-main'>
                                    <i className="fas fa-star rating-color me-1"></i>
                                    {i.product.ratingsAverage}
                                </span>
                            </div>

                        })}</div>
                        <div className="d-flex mt-4">
                            <p className="fw-bolder ">
                                Total order price:
                            </p>
                            <p className='fw-bold text-primary mx-4'>{order.totalOrderPrice}</p>
                        </div>

                    </div>
                </div>
            })}
        </>
    )
}
