
import React, { useContext, useState } from 'react';
import { Link } from 'react-router-dom';
import logo from '../../Asets/images/freshcart-logo.svg'
import { countContext } from '../../Context/context'
import { cartContext } from '../../Context/CartContext';
export default function Navbar() {
    function logout() {
        localStorage.removeItem('token')
    }
    const { islogedin, setIslogedin } = useContext(countContext)
    const { cartCount } = useContext(cartContext)


    const [pathName, setPathname] = useState(window.location.pathname)
    return (<>
        <nav className="navbar navbar-expand-sm  min-nav">
            <div className="container">
                <Link className="navbar-brand text-white fw-bold fs-5" to={"/home"}><img src={logo} alt="logo" /></Link>

                <button
                    className="navbar-toggler d-lg-none"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#collapsibleNavId"
                    aria-controls="collapsibleNavId"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                >
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="collapsibleNavId">
                    <ul className="navbar-nav me-auto mt-2 mt-lg-0">

                        <li className="nav-item">
                            <Link onClick={() => { setPathname("/home") }} className={pathName === "/home" ? "  text-main nav-link " : "nav-link"} to={"home"}>Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link onClick={() => { setPathname("/products") }} className={pathName === "/products" ? "  text-main nav-link " : "nav-link"} to={"products"}>products</Link>
                        </li>
                        <li className="nav-item">
                            <Link onClick={() => { setPathname("/categories") }} className={pathName === "/categories" ? "  text-main nav-link " : "nav-link"} to={"categories"}>Categories</Link>
                        </li>
                        {/* <li className="nav-item">
                            <Link onClick={() => { setPathname("/allorders") }} className={pathName === "/allorders" ? "  text-main nav-link " : "nav-link"} to={"allorders"}>Cart</Link>
                        </li> */}
                        <li className="nav-item">
                            <Link onClick={() => { setPathname("/orders") }} className={pathName === "/orders" ? "  text-main nav-link " : "nav-link"} to={"orders"}>Orders</Link>
                        </li>
                        <li className="nav-item">
                            <Link onClick={() => { setPathname("/brands") }} className={pathName === "/brands" ? "  text-main nav-link " : "nav-link"} to={"brands"}>Brands</Link>
                        </li>
                        <li className="nav-item">
                            <Link onClick={() => { setPathname("/wishlist") }} className={pathName === "/wishlist" ? "  text-main nav-link " : "nav-link"} to={"wishlist"}>Wishlist</Link>
                        </li>
                    </ul>
                    <ul className="navbar-nav ms-auto mt-2 mt-lg-0">
                        <li className="nav-item d-flex align-items-center ">
                            <Link className='text-black' to={"allorders"}> <i className="fa-solid fa-cart-shopping mx-1 fs-5 position-relative">
                                <span className={cartCount?.numOfCartItems == 0 || cartCount == 0 ? "disply-none opacity-0" : "position-absolute bottom-100 start-50 bg-main translate-middel rounded-circle shoppcart text-light py-1 text-center  mb-1"}>{cartCount?.numOfCartItems ? cartCount?.numOfCartItems : ""}</span>
                            </i></Link>
                            <i className="fa-brands fa-facebook mx-1 fs-5"></i>
                            <i className="fa-brands fa-twitter mx-1 fs-5"></i>
                            <i className="fa-brands fa-square-instagram mx-1 fs-5"></i>

                        </li>
                        {islogedin ? <li className="nav-item" onClick={() => { setIslogedin(false) ? logout() : logout() }}>
                            <Link onClick={() => { setPathname("/login") }} className={pathName === "/login" ? " nav-link text-main" : "nav-link"} to={"login"}>LOGOUT</Link>
                        </li> :
                            <><li className="nav-item">
                                <Link onClick={() => { setPathname("/regester"); }} className={pathName === "/regester" ? " nav-link text-main" : "nav-link"} to={"regester"}>REGESTER</Link>
                            </li><li className="nav-item">
                                    <Link onClick={() => { setPathname("/login"); }} className={pathName === "/login" ? " nav-link text-main" : "nav-link"} to={"login"}>LOGIN</Link>
                                </li></>}




                    </ul>

                </div>
            </div>
        </nav>

    </>

    )
}
