import axios from 'axios'
import { toast } from 'react-toastify';
import { Link } from 'react-router-dom'
import { useContext } from 'react';
import { cartContext } from '../../../Context/CartContext';
export default function Allproduct({ product }) {



  async function addProductToCart(productId) {
    const { data } = await axios.post(
      "https://ecommerce.routemisr.com/api/v1/cart",
      {
        productId,
      },
      {
        headers: {
          token: localStorage.getItem("token"),
        },
      }
    );
    toast(data.message, {
      position: "top-right",
      autoClose: 2000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: false,
      draggable: true,
      progress: undefined,
      theme: "dark",
    });
    console.log(product);
  }
  async function addProductToWishList(productId) {
    const { data } = await axios.post(
      "https://ecommerce.routemisr.com/api/v1/wishlist",
      {
        productId,
      },
      {
        headers: {
          token: localStorage.getItem("token"),
        },
      }
    );
    toast(" " + data.message, {
      position: "top-right",
      autoClose: 2000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });
    console.log(product);
  }

  const { setCartcount } = useContext(cartContext)
  async function addProductToCart(productId) {
    let { data } = await axios.post(`https://ecommerce.routemisr.com/api/v1/cart`, {
      productId
    }, {
      headers: { token: localStorage.getItem('token') }
    })
    setCartcount(data)
    toast.success(data.message);

  }

  return (
    <>

      <div className="product px-2 overflow-hidden py-3 cursor-pointer">
        <Link to={`/productdetails/` + product.id} className="a">

          <img src={product.imageCover} className="w-100" alt="" />
          <h5 className="font-sm text-main">{product.category.name}</h5>
          <h4 className="font-sm text-main">{product.title.split(" ").slice(0, 2).join(" ")}</h4>
          <p className="d-flex justify-content-between">
            <span> {product.price} EGP </span>
            <span>
              <i className="fas fa-star rating-color me-1"></i>
              {product.ratingsAverage}
            </span>

          </p>
        </Link>
        <Link onClick={() => addProductToCart(product.id)} className="btn bg-main text-wihte w-100">+Add to cart</Link>
        <button
          onClick={() => addProductToWishList(product.id)}
          className="btn bg-danger w-100 mt-1 text-white"
        >
          + Add To Wish List
        </button>
      </div>

    </>
  )
}
